const floatingAnim = (x: string, y: string) => keyframes`
0% {transform: translate3d(0px, 0%, 0px);}
10% {transform: translate3d(0px, 0%, 0px);}
49.9999% {transform: translate3d(0px, 0%, 0px);}
50% {transform: translate3d(0px, -50px, 0px);}
99.9999% {transform: translate3d(0px, -50px, 0px);}
`
const floatingAnim2 = (x: string, y: string) => keyframes`
0% {transform: translate3d(0px, 0%, 0px);}
10% {transform: translate3d(0px, 0%, 0px);}
32.9999% {transform: translate3d(0px, 0%, 0px);}
33% {transform: translate3d(0px, -50px, 0px);}
43% {transform: translate3d(0px, -50px, 0px);}
65.99999% {transform: translate3d(0px, -50px, 0px);}
66% {transform: translate3d(0px, -100px, 0px);}
100% {transform: translate3d(0px, -100px, 0px);}
`

